import Vue from 'vue'

export const store = Vue.observable({
  items: [
    "魏家凉皮",
    "油泼面",
    "山西刀削面",
    "高小松",
    "张亮麻辣烫",
    "驴肉火烧",
  ],
  history: []
})

export const mutations = {
  itemAdd(name) {
    store.items.push(name);
  },
  itemDel(idx) {
    store.items.splice(idx, 1);
  },
  reaHistory(name) {
    store.history.push(name);
  }
}